'use client';

import QuestionItem from '@/components/custom/QuestionItem';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { useAuth } from '@/context/AuthContext';
import { useDebounce } from '@/lib/hooks';
import { useRouter } from 'next/navigation';
import React, { useEffect, useState } from 'react';
import { Category } from '../../types.d';

// Helper component for when the page is loading data
const QuestionsLoadingSkeleton = () => (
  <div className="space-y-4 animate-pulse">
    <div className="h-16 bg-muted rounded-lg"></div>
    <div className="h-16 bg-muted rounded-lg"></div>
    <div className="h-16 bg-muted rounded-lg"></div>
  </div>
);

export default function QuestionsPage() {
  const { isAuthenticated, isLoading: isAuthLoading, user, logout } = useAuth();
  const router = useRouter();

  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // State for filters and pagination
  const [searchTerm, setSearchTerm] = useState('');
  const [difficulty, setDifficulty] = useState('all');
  const [page, setPage] = useState(1);
  const [pagination, setPagination] = useState({
    page: 1,
    totalPages: 1,
    totalResults: 0,
  });

  const debouncedSearchTerm = useDebounce(searchTerm, 300);

  useEffect(() => {
    if (!isAuthLoading && !isAuthenticated) {
      router.push('/login');
    }
  }, [isAuthenticated, isAuthLoading, router]);

  useEffect(() => {
    if (!isAuthenticated) return;

    const fetchData = async () => {
      setLoading(true);
      const params = new URLSearchParams({
        page: page.toString(),
        limit: '10',
        ...(debouncedSearchTerm && { search: debouncedSearchTerm }),
        ...(difficulty !== 'all' && { difficulty }),
      });

      try {
        const response = await fetch(
          `http://localhost:5000/api/v1/content?${params.toString()}`
        );
        if (!response.ok) throw new Error('Failed to fetch questions');
        const result = await response.json();
        setCategories(result.data);
        setPagination(result.pagination);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : 'An unknown error occurred'
        );
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [debouncedSearchTerm, difficulty, page, isAuthenticated]);

  if (isAuthLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        Authenticating...
      </div>
    );
  }

  if (!isAuthenticated) {
    return null;
  }

  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
    setPage(1); // Reset to page 1 on new search
  };

  const handleDifficultyChange = (value: string) => {
    setDifficulty(value);
    setPage(1); // Reset to page 1 on new filter
  };

  return (
    <main className="min-h-screen bg-background text-foreground p-4 sm:p-8 flex flex-col items-center">
      <div className="w-full max-w-4xl">
        <header className="flex justify-between items-center mb-12">
          <div>
            <h1 className="text-3xl font-bold">GeekHeaven DSA</h1>
            <p className="text-muted-foreground">Welcome, {user?.name}!</p>
          </div>
          <Button variant="outline" onClick={logout}>
            Logout
          </Button>
        </header>

        <Card className="mb-8">
          <CardContent className="p-4 flex flex-col sm:flex-row gap-4">
            <Input
              placeholder="Search for a question..."
              value={searchTerm}
              onChange={handleSearchChange}
              className="flex-grow"
            />
            <Select value={difficulty} onValueChange={handleDifficultyChange}>
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="Filter by difficulty" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Difficulties</SelectItem>
                <SelectItem value="Easy">Easy</SelectItem>
                <SelectItem value="Medium">Medium</SelectItem>
                <SelectItem value="Hard">Hard</SelectItem>
              </SelectContent>
            </Select>
          </CardContent>
        </Card>

        {loading ? (
          <QuestionsLoadingSkeleton />
        ) : error ? (
          <div className="text-center py-16 text-destructive">{error}</div>
        ) : categories.length > 0 ? (
          <Accordion type="single" collapsible className="w-full">
            {categories.map((category) => (
              <AccordionItem value={category._id} key={category._id}>
                <AccordionTrigger className="text-xl hover:no-underline">
                  {category.title}
                </AccordionTrigger>
                <AccordionContent>
                  <div className="space-y-3 pt-2">
                    {category.questions.map((question) => (
                      <QuestionItem key={question._id} question={question} />
                    ))}
                  </div>
                </AccordionContent>
              </AccordionItem>
            ))}
          </Accordion>
        ) : (
          <div className="text-center py-16">
            <h3 className="text-2xl font-semibold">No Questions Found</h3>
            <p className="text-muted-foreground mt-2">
              Try adjusting your search or filter criteria.
            </p>
          </div>
        )}

        {!loading && pagination.totalPages > 1 && (
          <div className="flex items-center justify-between mt-8">
            <Button onClick={() => setPage(page - 1)} disabled={page <= 1}>
              Previous
            </Button>
            <span className="text-muted-foreground">
              Page {pagination.page} of {pagination.totalPages}
            </span>
            <Button
              onClick={() => setPage(page + 1)}
              disabled={page >= pagination.totalPages}
            >
              Next
            </Button>
          </div>
        )}
      </div>
    </main>
  );
}
